/* top menu bar*/
.nav {
  margin-bottom: 1em;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  background-color: #4A4A6C;
  color: #fff;
  padding: 1em;
}
.nav > .main {
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
  text-align: left;
}
.nav >.right {
  font-size: 0.8em;
}
